@import "./css/normalize.min.css";

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px auto;
  padding: 0 5%;
}

.logo {
  width: 140px;
  margin-top: 15px;
}

.menu {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.menu li {
  margin: 0 15px;
}

.menu li:first-child {
  margin-left: 0;
}

.menu li:last-child {
  margin-right: 0;
}

a:link, a:visited, a:hover, a:active {
  font-family: 'Roboto', sans-serif;
  text-decoration: none;
  color: #212121;
  font-size: clamp(0.75rem, 2.25vw, 1.125rem);
}

.btn, a.btn {
  border-radius: 5px;
  padding: 8px 20px;
  color: #FFF;
  font-size: clamp(0.75rem, 2.25vw, 1.125rem);
  text-align: center;
}

.btn-primary, a.btn-primary {
  border: 1px solid #212121;
  color: #212121;
}

.btn-default, a.btn-default {
  background-color: #9e9e9e;
  border: 1px solid #9e9e9e;
}

.btn-info, a.btn-info {
  background-color: #1565c0;
  border: 1px solid #1565c0;
}

.btn-danger, a.btn-danger {
  background-color: #e53935;
  border: 1px solid #e53935;
}

.container {
  margin: 0 auto;
  max-width: 1400px;
}

hr.sep {
  border-top: 1px solid #212121;
  margin: 0;
}

.mr15 {
  margin-right: 15px;
}

.top-block  div.main {
  background: url(./img/earth.png) no-repeat;
  background-size: 45% auto;
  background-position: right bottom;
  padding: 17% 5% 10%;
  position: relative;
  margin-right: 5%;
}

.top-block h2 {
  font-size: clamp(1.25rem, 2.25vw, 2.875rem);
  font-weight: 500;
  margin: 0;
  margin-bottom: 35px;
}

.top-block h4:nth-child(3) {
  margin-bottom: 4px;
  margin-top: 40px;
}

.top-block h4:nth-child(4) {
  margin-bottom: 50px;
}

.top-block h4 {
  font-size: clamp(0.75rem, 2.25vw, 1.125rem);
  font-weight: 400;
  margin: 0;
}

.top-block .content {
  max-width: 500px;
}

.row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.column {
  flex: 50%;
}

.text-center {
  text-align: center;  
}

.responsive {
  width: 100%;
  height: auto;
}

.monetary h5, .supply h5,
.faucet h5, .mint h5 {
  font-size: clamp(1.25rem, 2.25vw, 2.25rem);
  font-weight: 500;
  margin-top: 0;
  margin-bottom: 35px;
}

.monetary p, .supply p,
.faucet p, .mint p {
  font-size: clamp(0.75rem, 2.25vw, 1.125rem);
  margin: 0 0 5px 0;
}

.monetary .btn, .supply .btn,
.faucet .btn, .mint .btn {
  margin-top: 25px;
  display: inline-block;
}

.monetary img {
  max-width: 420px;
}

.supply img {
  max-width: 450px;
}

.faucet img {
  max-width: 420px;
}

.mint img {
  max-width: 480px;
}

.monetary, .supply,
.faucet, .mint {
  max-width: 1215px;
}

.monetary {
  margin-top: 55px;
  margin-bottom: 55px;
  padding: 0 5%;
}

.supply {
  margin-top: 55px;
  margin-bottom: 55px;
  padding: 0 5%;
}

.faucet {
  margin-top: 55px;
  margin-bottom: 55px;
  padding: 0 5%;
}

.mint {
  margin-top: 55px;
  margin-bottom: 55px;
  padding: 0 5%;
}

.box {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* .box svg:first-child {
  margin-right: 15px;
} */

.footer {
  display: flex;
  margin-top: 50px;
  width: 650px;
  min-width: 40%;
  justify-content: space-around;
}

.footer ul {
  list-style: none;
  padding: 0;
}

.footer li {
  margin-bottom: 20px;
}

.footer a {
  color: #9e9e9e;
  font-size: clamp(1rem, 2.25vw, 1.125rem);
}

.footer h5 {
  color: #212121;
  font-size: clamp(1rem, 2.25vw, 1.25rem);
  font-weight: 500;
  margin: 25px 0;
}

.logo-icon {
  margin: 62px 0 30px 0;
}

.copyright {
  flex-direction: column;
  font-size: clamp(1rem, 2.25vw, 1.125rem);
  margin-bottom: 15px;
}

.copyright > span {
  margin-bottom: 10px;
  text-align: center;
}

.copyright a {
  font-size: clamp(1rem, 2.25vw, 1.125rem);
}

.overlay {
  height: 0%;
  width: 100%;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  background-color: #FFF;
  overflow: hidden;
  transition: 0.5s;
}

.overlay-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 55px 50px;
}

.overlay-content .btn {
  margin: 8px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: clamp(1rem, 2.25vw, 1.125rem);
  display: block;
  transition: 0.3s;
}

.hamburger-menu {
  z-index: 2;
}

.hamburger-menu.open {
  position: fixed !important;
  right: 10%;
}

.Cursor--blinking {
  animation: blink-animation 1s infinite;
  -webkit-animation: blink-animation 1s infinite;
  color: #1565c0;
}

.typing {
  display: inline;
}

.top-block .main a.btn:first-child {
  margin: 30px 15px 30px 0;
}
.top-block .main a.btn:last-child {
  margin-bottom: 50px;
}
.top-block .main a.btn {
  display: inline-block;
}

.page {
  min-height: 100vh;
  max-width: 800px;
  margin-top: 100px;
  margin-bottom: 100px;
  padding: 0 10%;
}

.page h3 {
  font-size: clamp(1.25rem, 2.25vw, 2.875rem);
  font-weight: 500;
  margin-bottom: 8%;
  color: #212121;
}

.page .get-started-item,
.page .get-grano-item span  {
  margin-bottom: 8%;
}

.page .get-started-item {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  border-top: 1px;
  border-bottom: 1px;
  border-left: 15px;
  border-right: 1px;
  border-style: solid;
  border-color: #cfd8dc;
  padding: 10px 0 10px 10px;
}

.page .get-started-item span,
.page .get-grano-item span {
  font-size: clamp(1rem, 2.25vw, 1.7rem);
  font-weight: 500;
  margin-bottom: 15px;
  margin-top: 15px;
  color: #212121;
}

.page .get-started-item p,
.page .get-grano-item p,
.page .disclaimer,
.page.about p, .page.about span {
  color: #212121;
  font-size: clamp(0.75rem, 2.25vw, 1.125rem)
}

.page.about h4,
.page.about p>span:first-child {
  font-weight: 500;
}

.page.about a.blue-link, .page.about a.blue-link:hover {
  color: #1565c0;
}

.page.about h4 {
  font-size: clamp(1rem, 2.25vw, 1.7rem);
  margin-top: 12%;
  margin-bottom: 8%;
}

.page .disclaimer {
  margin: 0;
  padding: 0;
  list-style: none;
}

.page .disclaimer ul {
  padding-left: 30px;
  list-style-type: none;
}

.page .disclaimer span {
  font-weight: 500;
}

.page .disclaimer p {
  margin: 0;
}

ul.disclaimer li {
  margin-bottom: 10px;
}
ul.disclaimer ul>li:first-child {
  margin-top: 10px;
}

.no-mb {
  margin-bottom: 0;
}

.mb15 {
  margin-bottom: 15px;
}

.mt50 {
  margin-top: 50px !important;
}

.mt-60 {
  margin-top: 60px !important;
}

ol {
  list-style: none;
  counter-reset: item;
  padding: 0;
}

ol li > span {
  color: #212121;
  font-size: clamp(1rem, 2.25vw, 1.7rem);
  font-weight: 500;
}

span.title {
  display: block;
  color: #455A64;
  margin-bottom: 10px;
  font-weight: 500;
}

ol p, ol p, span.title {
  font-size: clamp(0.75rem, 2.25vw, 1.125rem);
}

ol div {
  margin-bottom: 30px;
}

ol ol {
  padding-left: 30px;
}

ol ol > li span {
  font-size: clamp(0.9rem, 2.25vw, 1.3rem);
}

ol > li {
  counter-increment: item;
  margin-top: 12%;
}

ol ol > li {
  display: block;
  margin-top: 8%;
}

ol > li:before {
  content: counters(item, ".") ". ";
  color: #cfd8dc;
  font-size: clamp(1rem, 2.25vw, 1.7rem);
}
ol ol > li:before {
  content: counters(item, ".") " ";
  font-size: clamp(0.9rem, 2.25vw, 1.3rem);
}

.page .get-grano-item .btn-swap {
  border-radius: 10px;
  padding: 5px 20px;
  display: inline-flex;
  align-items: center;

  background-color: #eeeeee;
  border: 1px solid #eeeeee;
  margin-right: 25px;
  margin-top: 35px;
}

.page .get-grano-item .btn-swap img {
  width: 100%;
  height: auto;
}

.page .get-grano-item .btn-swap > span {
  font-size: 1.5rem; 
  font-weight: 700; 
  margin-left: 15px;
}

.btn-container {
  text-align: center;
  margin: 10% 0;
}

.bg {
  background-color: #ECEFF1;
  min-height: calc(100vh - 80px);
}
.bg-splash {
  height: calc(100vh - 80px);
  padding: 0 30px;
}

.bg-faucet a {
  color: #1565c0;
}

.dapp-header {
  background-color: #212121;
  padding: 10px 0px;
}

.dapp-container {
  max-width: 800px;
  margin: 0 auto;
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  /* min-height: 80px; */
  color: #FFF;
  font-size: 1.875rem;
  padding: 0 30px;
}

.header-content svg {
  margin-top: 11px;
  margin-right: 10px;
}

.header-content .text {
  border-left: 1px solid #FFF;
  padding-left: 10px;
}

.dapp-container .network {
  font-size: 0.6875rem;
  text-transform: uppercase;
  color: #212121;
  background-color: #cfd8dc;
  padding-top: 7px;
  padding: 5px 15px 5px 15px;
  line-height: 11px;
  border-radius: 5px;
  margin-left: 30px;
}

.connect-to {
  text-align: center;
}

.connect-to h3 {
  font-size: clamp(1rem, 2.25vw, 1.6rem);
  font-weight: 400;
  margin: 0;
  padding-top: 80px;
  padding-bottom: 45px;
}

.connect-to button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  border: 5px solid #FFF;
  background: #FFF;
  padding: 10px 30px;
  outline: none;
  cursor: pointer;
  font-size: clamp(1rem, 2.25vw, 1.6rem);
}
.connect-to button > svg {
  margin-right: 15px;
}

.content {
  border-radius: 5px;
}

.account.rows {
  display: flex;
  justify-content: space-between;
}

.account.rows > .col {
  padding: 25px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.account.rows > .col:last-child {
  border-left: 1px solid #ECEFF1;
}

.account.rows .col>div:first-child {
  margin:0 15px 0 0;
}

.account.rows .balance-info,
.account.rows .address-info {
  display: flex;
  flex-direction: column;
  height: 60px;
  justify-content: space-between;
}

.account.rows .balance-info > div,
.account.rows .address-info > div {
  font-size: clamp(0.9rem, 2.25vw, 1.125rem);
}

.fau .block-item h4 {
  margin: 5px 0 22px 0;
}

.flex-start {
  display: flex;
  justify-content: flex-start;
  color: #43a047;
}

.fau .block-item p {
  color: #b0bec5;
  margin: 36px 0 22px 0;
  font-size: clamp(0.9rem, 2.25vw, 1.125rem);
}

.fau .block-item button {
  padding: 8px 20px;
  text-transform: uppercase;
  background-color: #43a047;
  color: #FFF;
  border-radius: 4px;
  border: 1px solid #43a047;
  line-height: normal;
  outline: none;
  cursor: pointer;
  font-size: clamp(0.9rem, 2.25vw, 1rem);
}

.fau .block-item  button:disabled {
  cursor: default;
  background-color: #9e9e9e;
  border: 1px solid #9e9e9e;
}

.history {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: clamp(0.9rem, 2.25vw, 1.125rem);
  margin-top: 36px;
}

.history > div {
  flex: 1 0 auto;
}

.history .head {
  color: #b0bec5;
  margin-bottom: 5px;
}

.history .head + div {
  font-size: clamp(0.8rem, 2.25vw, 0.9rem);
}

div.paper {
  border-radius: 10px !important;
}

.account-info {
  background: #FFF;
  border-radius: 5px;
}

.minter .block-item,
.fau .block-item {
  padding: 25px;
  background: #FFF;
  border-radius: 5px;
  margin: 32px 0;
  font-size: (0.9rem, 2.25vw, 1rem);
  position: relative;
}

.block-item > span,
.fau > span {
  color: #212121;
  background-color: #CFD8DC;
  text-transform: uppercase;
  font-size: clamp(0.9rem, 2.25vw, 1rem);
  font-weight: 500;
  padding: 10px 20px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  position: absolute;
  left: 25px;
  top: 0;
}

.btn-burn {
  color: #FFF;
  padding: 8px 20px;
  background-color: #e53935;
  border-radius: 5px;
  border: 1px solid #e53935;
  text-transform: uppercase;
  white-space: nowrap;
  line-height: normal;
  outline: none;
  cursor: pointer;
  font-size: clamp(0.9rem, 2.25vw, 1rem);
} 

.btn-burn.lg {
 display: block;
}
.btn-burn.xs {
  display: none;
 }

.btn-disabled {
  color: #FFF;
  padding: 8px 20px;
  background-color: #9e9e9e;
  border-radius: 5px;
  border: 1px solid #9e9e9e;
  text-transform: uppercase;
  white-space: nowrap;
  outline: none;
  font-size: clamp(0.9rem, 2.25vw, 1rem);
}

.minter .account-info,
.minter .block-item,
.fau .account-info,
.fau .block-item {
  margin-left: 30px;
  margin-right: 30px;
}

.btn-mint {
  color: #FFF;
  padding: 8px 20px;
  background-color: #43a047;
  border-radius: 5px;
  border: 1px solid #43a047;
  text-transform: uppercase;
  line-height: normal;
  outline: none;
  cursor: pointer;
  white-space: nowrap;
  font-size: clamp(0.9rem, 2.25vw, 1rem);
}

.block-item input {
  border-top: 0;
  border-left: 0;
  border-right: 0;
  border-bottom: 1px solid #b0bec5;
  outline: none;
  width: 75%;
  max-width: 170px;
}

.block-item button.max {
  background: none;
  border: 0;
  outline: none;
  font-size: clamp(0.7rem, 2.25vw, 0.9rem);
  color: #1565c0;
  text-transform: uppercase;
  cursor: pointer;
  padding: 0;
}

.block-item .field > div:first-child {
  color: #90A4AE;
  margin-top: 36px;
  margin-bottom: 10px;
  font-weight: 500;
  font-size: clamp(0.9rem, 2.25vw, 1.125rem);
}

.no-break {
  white-space: nowrap;
  font-size: clamp(0.7rem, 2.25vw, 0.9rem);
}

.rec-item {
  margin-top: 25px;
  border-bottom: 1px solid #cfd8dc;
}

.rec-item:first-of-type {
  margin-top: 36px;
}

.rec-item > span {
  padding: 7px;
  color: #FFF;
  font-size: clamp(0.9rem, 2.25vw, 1rem);
  border-radius: 5px;
  background-color: #212121;
  display: inline-block;
  margin-bottom: 16px;
}

.rec {
  justify-content: space-between;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: auto;
  align-content: flex-start;
}

.rec-item:nth-last-child(2) {
  margin-bottom: 25px;
}

.rec-item > .rec,
.rec-item:nth-last-child(2) {
  margin-bottom: 25px;
}

.rec-item > .rec:last-child {
  margin-bottom: 25px;
}

.rec-col {
  flex: 0 0 auto;
  margin: 15px 15px 15px 0;
}

.rec-col > div:first-child {
  font-size: clamp(0.9rem, 2.25vw, 1.125rem);
  color: #90A4AE;
  margin-bottom: 5px;
}

.rec-col > div:last-child {
  font-size: clamp(0.8rem, 2.25vw, 0.9rem);
  color: #212121;
  display: block;
}

.rec-col .status {
  margin-top: 10px !important;
}

.prog-flex {
  display: flex; 
  align-items: center; 
}

.progress {
  height: 15px;
  position: relative;
  background: #cfd8dc;
  border-radius: 4px;
  display: block;
  margin-bottom: 5px;
}

.progress > span {
  display: block;
  border-radius: 4px;
  height: 100%;
  background-color: #81c784;
}

.block-item a {
  outline: none;
  font-size: clamp(0.8rem, 2.25vw, 0.9rem);
  color: #1565c0;
}

.block-item  button.load-more {
  background: none;
  border: 1px solid #212121;
  outline: none;
  font-size: clamp(0.9rem, 2.25vw, 1rem);
  color: #212121;
  cursor: pointer;
  border-radius: 5px;
  padding: 8px 20px;
}

.block-item .no-records {
  font-size:  clamp(0.9rem, 2.25vw, 1.125rem);
  color: #90A4AE;
  font-weight: 500;
  margin-top: 50px;
  margin-bottom: 18px;
}

.block-item .field .fore-title {
  font-size: clamp(0.9rem, 2.25vw, 1.125rem);
  color: #90A4AE;
  font-weight: 500;
  margin-left: 25px;
}

.block-item .field .fore-title:first-child {
  font-size: clamp(0.9rem, 2.25vw, 1.125rem);
}

.block-item .field .fore-val {
  margin-top: 8px;
  margin-bottom: 18px;
  font-size: clamp(0.7rem, 2.25vw, 0.9rem);
  margin-left: 25px;
}

.progress > i {
  position: absolute;
  left: 12px;
  top: 1px;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex {
  display: flex;
}

.flex > div {
  flex: 1 0 50%;
}

.mtb22 {
  margin-top: 22px;
  margin-bottom: 22px;
}
.pt45 {
  padding-top: 45px;
}

.get-started-item:hover {
  border-color:#212121;
  background-color: #212121;
}

.get-started-item:hover > span, 
.get-started-item:hover > p  {
  color: #FFF;
}

@keyframes blink-animation {
  0%   {opacity: 1;}
  50%  {opacity: 0;}
  100% {opacity: 1;}
}

@-webkit-keyframes blink-animation {
  0%   {
    opacity: 1;
  }
  50%  {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media screen and (min-width: 821px) {
  .hamburger-menu {
    display: none;
  }

  .hamburger-menu.open + #nav {
    height: 0 !important;
  }
}

@media (max-width:768px) {
  .rec {
      justify-content: start;
  }
  .footer > div {
    flex: 1 0 34%;
    padding-left: 16%;
  }
}

@media screen and (max-width: 1400px) {
  header {
    padding: 0 10%;
  }

  .monetary, .supply,
  .faucet, .mint {
    padding: 0 10%;
  }

  .top-block  div.main {
    padding: 17% 5% 10% 10%;
    margin-right: 5%;
  }
}
@media screen and (max-width: 820px) {
  .menu {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  .footer {
    flex-wrap: wrap;
  }

  .rec {
    flex-wrap: wrap;
  }

  .rec-col {
    flex: 0 26%;
  }
}

@media screen and (max-width: 503px) {
  .account.rows {
    flex-direction: column;
  }
  .account.rows > .col:first-child {
    order: 2;
  }
  .account.rows > .col:last-child {
    order: 1;
    border-bottom: 1px solid #cfd8dc;
    border-left: 0;
  }

  .flex {
    flex-direction: column;
    flex-wrap: nowrap;
    display: inline;
  }

  .rec {
    flex-direction: column;
    flex-wrap: wrap;
  }

  .block-item .field .fore-title,
  .block-item .field .fore-val {
    margin-left: 0;
  }

  .btn-burn.lg {
    display: none;
  }
  .btn-burn.xs {
    display: block;
  }
}

@media screen and (max-width: 500px) {
  header, .page, .copyright {
    padding: 0 15% !important;
  }

  .top-block div.main {
    background: none;
  }

  .monetary, .supply,
  .faucet, .mint {
    padding: 0 15%;
  }

  .hamburger-menu.open {
    right: 15%;
  }

  .top-block  div.main {
    padding: 17% 15% 10%;
    margin-right: 0;
  }

  .row {
    flex-direction: column;
  }

  .monetary img, .supply img,
  .faucet img, .mint img {
    display: none;
  }

  .btn, a.btn {
    display: block !important;
    margin-right: 0 !important;
  }
  .top-block div.main {
    background-position: 80% 100%;
  }

  .footer {
    flex-direction: column;
  }

  .bg-faucet .history {
    flex-direction: column;
    align-items: baseline;
  }

  .page .get-grano-item .btn-swap {
    margin-right: 0;
  }

  .history > div, .rec-item .rec-col {
    margin-bottom: 20px;
  }

  .history > div:last-child,
  .rec-item .rec-col:last-child {
    margin-bottom: 0;
  }
  .progress > i {
    top: 2px;
    font-style: normal;
  }
}

@media screen and (max-width: 400px) {
  .header-content {
    flex-direction: column;
  }
  .dapp-container .network {
    margin-top: 10px;
    margin-left: 0;
  }
}

@media screen and (max-width: 859px) {
  .logo {
    width: 100px;
    margin-top: 12px;
  }
}
